import Vue from 'vue'
import VueRouter from 'vue-router'
import Info from '../Info.vue'
import Page from '../Page.vue'

Vue.use(VueRouter)

export default new VueRouter({
    mode: 'hash',
    base: "/XanthusDocs",
    routes: [
      {
        path: '/',
        name: "Page",
        component: Page,
      },
      {
        path: '/Info',
        name: "Info",
        component: Info,
        meta: {layout: 'empty'}
      }
    ]
  })